.div {
    margin: 20px auto;
    width: 800px;

    display: flex;
    align-items: top;

    border-radius: 0 10px 10px 10px;
    background-color: #616164;


    & div {
        margin: 0 20px;


        & section {
            margin: 50px 0;


            & h2 {
                margin: 3px 0;
            }
            & hr {
                // margin: 0 10px 10px 10px;

                border: none;
                border-top: solid 0.1px white;
            }

            & ul {
                margin: 0;
                padding: 0;
                list-style-type: none;
        
        
                & h3, & h4 {
                    margin: 15px 0 0 0;
                }
                & h5 {
                    margin: 0;

                    font-size: 18px;
                    font-weight: 300;
                }
                & h6 {
                    margin: 0 0 10px 0;

                    font-weight: 400;
                }
                & span {
                    display: block;
                }
            }

            & .pointList {
                margin: 0 30px;
                list-style-type: disc;
            }


            & .image {
                position: absolute;
                width: 80px;

                border-radius: 10px;
            }
            & .bypassImage {
                margin: 0 0 0 100px;
            }
        }
    }
        
}