.div {
    margin: 40px auto;
    width: 600px;

    border: solid 2px #7597c1;
    border-radius: 10px;
    box-shadow: 10px 10px 10px rgb(26, 26, 26);


    & #main {
        width: 600px;
        height: 300px;

        
        & img, & article {
            position: absolute;
            width: 600px;
            height: 300px;

            border-radius: 10px 10px 0 0;
        }
        & article {
            background-color: #56565a;
            opacity: 0.98;


            & h2{
                margin: 30px 50px 20px 50px;
            }
            & p {
                margin: 5px 50px;
            }

            & div {
                margin: 30px 50px;
                position: absolute;
                bottom: 0px;
                display: flex;
                align-items: center;


                & span {
                    bottom: 10px;
                    margin: 0 10px 0 0;
                }
            }
        }
    }

    & #secondary {
        height: 42px;

        border-radius: 0 0 10px 10px;

        background-color: #646468;


        & .HorizontalArrangement {
            display: flex;
            align-items: center;
        }
    
        & #left {
            margin: 0 10px;
            float: left;
        }
        & #right {
            margin: 0 10px;
            float: right;


            & a {
                height: 30px;
                margin: 5px 3px;
                padding: 0 12px 2px 12px;

                display: flex;
                align-items: center;

                border-radius: 0 5px 5px 5px;

                background-color: #7d7d83;
                color: white;

                text-decoration: none;
            }
        }
    }
}