.div {
    margin: 0 auto;
    width: 400px;
    height: 500px;


    & section {
        margin: 0 0 40px 0;
        text-align: center;
    }

    & .link {
        display: block;
        margin: 10px 0 10px -4px;
        position: relative;
        width: 400px;
        height: 100px;

        overflow: hidden;
        border: solid 4px rgba(0, 0, 0, 0);
        border-radius: 0 10px 10px 10px;

        cursor: pointer;

        transition: border .15s;


        &:hover {
            border: solid 4px white;
        }

        & p {
            position: absolute;
            left: 30px;
            bottom: -10px;

            font-size: 20px;
            font-weight: 500;

            color: white;
        }
    }
    & #linkEmail {
        background-color: #a4c1ff;
    }
    & #linkLinkedin {
        background-color: #d2abff;
    }
    & #linkInstagram {
        background-color: #f693ff;
    }

    & .icon {
        position: absolute;
        opacity: 0.6;
    }
    & #email {
        right: 20px;
        top: -40px;
        width: 200px;
        opacity: 0.7;
    }
    & #linkedin {
        right: 45px;
        top: -25px;
        width: 150px;
    }
    & #instagram {
        right: 33px;
        top: -30px;
        width: 170px;
    }
}